<template>
<div class="card">
  <img class="card-img-top" :src="file.info.image" alt="episode image">
  <div class="card-body">
    <p class="card-text">S{{pad(file.info.season)}}E{{pad(file.info.number)}}</p>
    <h4 class="card-title">{{file.info.name}}</h4>
    <p class="card-text">{{file.info.summary}}</p>
    <div class="button-group">
      <button type="button" class="btn btn-secondary btn-sm" @click="() => watch(file)">Watch</button>
      <button type="button" class="btn btn-secondary btn-sm" @click="() => copy(file)">✂</button>
    </div>
  </div>
  <div class="card-footer">
    <small class="text-muted">{{(file.size * 1e-6).toFixed(0)}} MB</small>
  </div>
</div>
</template>

<script>
export default {
  name: 'FileCard',
  props: ['file'],
  methods: {
    pad(num) {
      return num < 10 ? '0' + num : num;
    },
    copy(file) {
      const addr = `http://localhost:3000/api/video/${file.id}`;
      const textArea = document.createElement('textarea');
      textArea.value = addr;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        const successful = document.execCommand('copy');
        const msg = successful ? 'successful' : 'unsuccessful';
        console.log('Copying of link was ' + msg);
      } catch (err) {
        console.log('Copying of link failed.');
      }
      document.body.removeChild(textArea);
    },
    watch: async(file) => {
      try {
        const addr = `/api/link/${file.id}`;
        const data = await fetch(addr).then(res => res.blob());
        const a = document.createElement('a');
        const url = URL.createObjectURL(data);
        a.href = url;
        a.download = 'playlist.vlc';
        a.click();
        URL.revokeObjectURL(url);
      } catch (err) {
        console.log(err);
      }
    }
  }
}
</script>
<style scoped>
.card {
  flex: 0 1 calc((100% - 6rem) / 3);
  margin-bottom: 2rem;
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-img-top {
  max-height: 270px;
}
</style>



